<template>
  <div class="create-baby-container">
    
    <div class="new-user-cue f-i-c">
      <img src="@/assets/images/verify_code_tip.png" alt="">
      <p>
        再完善一下宝宝信息就OK啦
      </p>
    </div>

    <div class="sex-input-cue f-c">
      选择宝宝性别
    </div>
    
    <div class="sex-choose-box f-row-between">
      <div
        @click="babyInfo.sex = 0"
        :class="['sex', 'f-row-between-c', babyInfo.sex === 0 ? 'sex-boy-back' : '']"
      >
        <img src="@/assets/images/baby_boy.png" alt="">
        <p>男生</p>
      </div>
      <div
        @click="babyInfo.sex = 1"
        :class="['sex', 'f-row-between-c', babyInfo.sex === 1 ? 'sex-girl-back' : '']"
      >
        <img src="@/assets/images/baby_girl.png" alt="">
        <p>女生</p>
      </div>
    </div>

    <div class="baby-name f-c">
      <van-field
        input-align="center"
        clearable
        v-model="babyInfo.name"
        placeholder="输入宝宝昵称"
      />
    </div>

    <div @click="datePicker.isShowDatePicker = true;relationPicker.isShowRelationPicker = false" class="baby-birth f-i-c">
      <div
        :class="['birth', 'f-c', babyInfo.birth === '' ? 'birth-empty' : '']"
      >
        {{ babyInfo.birth || '选择宝宝生日' }}
      </div>
      <img src="@/assets/images/drop_down.png" alt="">
    </div>

    <div @click="relationPicker.isShowRelationPicker = true;datePicker.isShowDatePicker = false" class="baby-birth f-i-c">
      <div
        :class="['birth', 'f-c', babyInfo.relation === '' ? 'relation-empty' : '']"
      >
        {{ babyInfo.relation || '选择和宝宝的关系' }}
      </div>
      <img src="@/assets/images/drop_down.png" alt="">
    </div>

    <van-button
      @click="$router.push( '/my/my-baby/list' )"
      v-if="!relationPicker.isShowRelationPicker && !datePicker.isShowDatePicker"
      class="button-finish"
      type="default"
      text="完成"
      round
      color="linear-gradient(90deg,rgba(246,197,67,1),rgba(239,140,56,1))"
    />

    <div class="picker-area">
      <van-datetime-picker
        v-if="datePicker.isShowDatePicker"
        v-model="datePicker.chooseDate"
        type="date"
        :min-date="datePicker.minDate"
        :max-date="datePicker.maxDate"
        @confirm="confirmBirth"
        @cancel="datePicker.isShowDatePicker = false"
      />
      <van-picker
        show-toolbar
        v-if="relationPicker.isShowRelationPicker"
        :columns="relationPicker.options"
        @confirm="confirmRelation"
        @cancel="relationPicker.isShowRelationPicker = false"
      />
    </div>

  </div>
</template>

<script>
import Vue from 'vue';
import { Field, DatetimePicker, Picker, Button } from 'vant';

Vue.use(Field).use(DatetimePicker).use(Picker).use(Button);

export default {
  data() {
    return {
      babyInfo: {
        sex: 0, // 0: 男 1: 女
        name: '',
        birth: '',
        relation: ''
      },
      
      datePicker: {
        minDate: new Date(1970, 0, 1),
        maxDate: new Date(2025, 10, 1),
        chooseDate: new Date(),
        isShowDatePicker: false
      },

      relationPicker: {
        options: ['妈妈', '爸爸', '爷爷', '奶奶', '外公', '外婆'],
        isShowRelationPicker: false
      }
    }
  },

  created() {
    document.title = '青豆家长';
  },

  methods: {
    confirmBirth(value) {
      let year = new Date( value ).getFullYear(),
          month = new Date( value ).getMonth() + 1,
          date = new Date( value ).getDate();
      this.babyInfo.birth = `${year}-${month}-${date}`;
      this.datePicker.isShowDatePicker = false;
    },

    confirmRelation(value, index) {
      this.babyInfo.relation = value;
      this.relationPicker.isShowRelationPicker = false;
    }
  }
}
</script>

<style lang="scss" scoped>
.create-baby-container {
  width: 100%;
  min-height: 100%;
  padding: 30px 20px 0;
  box-sizing: border-box;
  background-color: #fff;

  .new-user-cue {
    width: 100%;
    margin-bottom: 40px;

    img {
      width: 60px;
      height: 60px;
    }
    p {
      font-size: 20px;
      font-weight: 500;
      color: rgba(34,34,34,1);
      margin-left: 16px;
    }
  }

  .sex-input-cue {
    margin-bottom: 20px;
    width: 100%;
    font-size: 15px;
    color: rgba(187,187,187,1);
  }

  .sex-choose-box {
    width: 100%;
    margin-bottom: 24px;

    .sex {
      width: calc(50% - 12.5px);
      height: 90px;
      border-radius: 10px;
      padding: 15px 15px 15px 12px;

      img {
        width: 73px;
        height: 60px;
      }
      p {
        width: calc(100% - 90px);
        font-size: 18px;
        font-weight: 600;
        color: rgba(102,102,102,1);
      }
    }
    .sex-boy-back {
      background-color: #D0ECFC;
    }
    .sex-girl-back {
      background-color: #FFDEE3;
    }
  }

  .baby-name {
    font-size: 14px;
    width: 100%;
    height: 44px;
    border: 1px solid rgba(221,221,221,1);
    border-radius: 22px;
    padding: 2px 5px;
    box-sizing: border-box;
    margin-bottom: 24px;

    .van-field {
      padding: 0 16px;
    }
  }
  .baby-birth {
    font-size: 14px;
    width: 100%;
    height: 44px;
    border: 1px solid rgba(221,221,221,1);
    border-radius: 22px;
    padding: 2px 18px;
    box-sizing: border-box;
    margin-bottom: 24px;
    position: relative;

    .birth-empty, .relation-empty {
      color: #969799;
    }
    .birth {
      width: 100%;
    }
    img {
      position: absolute;
      width: 12px;
      height: 7px;
      right: 18px;
    }
  }

  .button-finish {
    width: calc(100% - 40px);
    height: 45px;
    position: absolute;
    bottom: 40px;
    left: 20px;
  }

  .picker-area {
    width: calc(100% - 40px);
    box-sizing: border-box;
    position: absolute;
    bottom: 0;
  }
}
</style>


